import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

import Home from '../components/Home'
import Phone from '../components/Phone'
import Angular from '../components/Angular'
import Vues from '../components/Vues'
import React from '../components/React'
import Ng from '../components/Ng'
import Mode from '../components/Mode'
import Login from '../components/Login'

import New from '../components/home/New'
import Hot from '../components/home/Hot'
import Node from '../components/home/Node'
import Angulars from '../components/home/Angular'
import Mongodb from '../components/home/Mongodb'
import Reacts from '../components/home/React'
import Linux from '../components/home/Linux'
import Git from '../components/home/Git'
import Test from '../components/home/Test'
import Keep from '../components/home/Keep'
import Docker from '../components/home/Docker'

import News from '../components/home/News'

export default new VueRouter({
  mode: 'history',
  scrollBehavior: () => ({y: 0}),
  routes: [
    {path: '/', redirect: '/home'},
    {
      path: '/home',
      component: Home,
      children: [
        {path: '/', redirect: 'new'},
        {path: 'new', component: New},
        {path: 'hot', component: Hot},
        {path: 'node', component: Node},
        {path: 'angular', component: Angulars},
        {path: 'mongodb', component: Mongodb},
        {path: 'react', component: Reacts},
        {path: 'linux', component: Linux},
        {path: 'git', component: Git},
        {path: 'test', component: Test},
        {path: 'keep', component: Keep},
        {path: 'docker', component: Docker},
        {path: 'news/:_id', name: 'news', component: News}
      ]
    },
    {path: '/phone', component: Phone},
    {path: '/angular', component: Angular},
    {path: '/vues', component: Vues},
    {path: '/react', component: React},
    {path: '/ng', component: Ng},
    {path: '/mode', component: Mode},
    {path: '/login', component: Login}
  ]
})



