<template>
  <div class="content">
    <button v-on:click="handleLink">{{handleText}}</button>
    hot.vue
  </div>
</template>

<script>
export default {
  name: 'hot',
  data () {
    return {
      handleText: 'go to New'
    }
  },
  created () {
    this.$http.get('https://api.jackhu.top/article/getFrontArticleList?currentPage=1&itemsPerPage=10&sortName=visit_count&tagId=').then(function (res) {
      let data = res.body
      console.log(data)
    })
  },
  methods: {
    handleLink: function () {
      this.$router.push('new')
    }
  }
}
</script>


