<template>
  <div class="article-box">
    <div class="article-container">
      <h1 class="title">{{article.title}}</h1>
      <p>阅读{{article.visit_count}} 评论{{article.comment_count}} 喜欢{{article.like_count}}</p>
      <div class="content" v-html="article.content"></div>
    </div>
    <div class="prenext" v-if="page != null">
      <div class="text-left prev" v-if="page.prev != null">
        <router-link :to="{name:'news', params: {_id: page.prev._id}}">
          上一篇:{{page.prev.title}}
        </router-link>
      </div>
      <div class="text-right next" v-if="page.next != null">
        <router-link :to="{name:'news', params: {_id: page.next._id}}">
          下一篇:{{page.next.title}}
        </router-link>
      </div>
    </div>
    <div class="comment-container clearfix">
      <div class="comment-head clearfix">
        <i>{{comments.length}}条评论</i>
        <div class="goto-comment pull-right" @click="addComment"><i class="glyphicon glyphicon-pencil"></i>添加新评论</div>
      </div>
      <div class="comment_list">
        <div class="comment-item" v-for="comment in comments">
          <div class="content">
            <div class="meta-top">
              <a class="avatar" v-if="comment.user_id != null"><img alt="comment.user_id.nickname" :src="comment.user_id.avatar"></a>
              <a class="author-name link-light" v-if="comment.user_id != null">{{comment.user_id.nickname}}</a>
              <span class="reply-time"> {{comment.created.substr(0,10)+' '+comment.created.substr(11,5)}} </span>
            </div>
            <p class="comment-content">{{comment.content}}</p>
            <div class="comment-footer text-right">
              <a class="reply" href="javascript:;">回复</a>
            </div>
            <div class="reply-list" v-if="comment.replys.length !== 0">
              <div class="reply-item" v-for="reply in comment.replys">
                <p class="reply-content"><a class="reply-user link-light">{{reply.user_info.nickname}}</a>： {{reply.content}} </p>
                <div class="reply-footer text-right">
                  <a class="reply" href="javascript:;">回复</a>
                  <span class="reply-time pull-left">{{reply.created.substr(0,10)+' '+reply.created.substr(11,5)}}</span>
                </div>
              </div>
            </div>
            <form class="new-reply hide" id="replyForm2">
              <div class="comment-text">
                <textarea required="" maxlength="2000" placeholder="写下你的回复…" id="replyContent2"></textarea>
                <div><input type="submit" value="发 表" class="btn btn-sm btn-info"></div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'news',
  data () {
    return {
      article: {},
      comments: {},
      page: {}
    }
  },
  methods: {
    addComment: function () {

    }
  },
  created () {
    this.$http.get('https://api.jackhu.top/article/' + this.$route.params._id + '/getFrontArticle').then(function (res) {
      this.article = res.body.data
    })
    this.$http.get('https://api.jackhu.top/comment/' + this.$route.params._id + '/getFrontCommentList').then(function (res) {
      this.comments = res.body.data
    })
    this.$http.get('https://api.jackhu.top/article/' + this.$route.params._id + '/getPrenext?currentPage=1&itemsPerPage=10&sortName=publish_time&tagId=').then(function (res) {
      this.page = res.body.data
    })
  },
  watch: {
    $route: {
      handler: function (val, oldVal) {
        this.$http.get('https://api.jackhu.top/article/' + this.$route.params._id + '/getFrontArticle').then(function (res) {
          this.article = res.body.data
        })
        this.$http.get('https://api.jackhu.top/comment/' + this.$route.params._id + '/getFrontCommentList').then(function (res) {
          this.comments = res.body.data
        })
        this.$http.get('https://api.jackhu.top/article/' + this.$route.params._id + '/getPrenext?currentPage=1&itemsPerPage=10&sortName=publish_time&tagId=').then(function (res) {
          this.page = res.body.data
        })
      },
      deep: true
    }
  }
}
</script>


