import Vue from 'vue'
import Resource from 'vue-resource'
import App from './App'
import router from './router'

import 'bootstrap/dist/css/bootstrap.css'
import './assets/css/style.css'

Vue.use(Resource)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



