<template>
  <div class="content">
    <ul class="container-fluid">
      <li class="list row" v-for="row in rows">
        <router-link :to="{name:'news', params: {_id: row._id}}">
          <div class="col-xs-8 col-sm-9 col-md-10 info">
            <div class="time">{{row.publish_time.substr(0,10)+'&nbsp;&nbsp;'+row.publish_time.substr(11,8)}}</div>
            <div class="title">{{row.title}}</div>
            <div class="mark">阅读 {{row.visit_count}} · 评论 {{row.comment_count}} · 喜欢 {{row.like_count}}</div>
          </div>
          <div class="col-xs-4 col-sm-3 col-md-2 img">
            <img v-if="row.images.length !== 0" :src="row.images[0].url">
          </div>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'new',
  data () {
    return {
      rows: []
    }
  },
  created () {
    this.$http.get('https://api.jackhu.top/article/getFrontArticleList?currentPage=1&itemsPerPage=10&sortName=visit_count&tagId=').then(function (res) {
      this.rows = res.body.data
    })
  }
}
</script>


